import React from "react";
import "../styles/About.css";

function About() {
  return (
    <div className="about">
      <h2>About this App</h2>
      <p>
        This is a simple Pokdex web application built with React and PokAPI.
      </p>
      <p>
        It displays a list of Pokmon, their details, and allows for pagination.
      </p>
    </div>
  );
}

export default About;
